import { useReducer } from 'preact/hooks';
import type { MessageToastProps } from '#UNSAFE_MessageToast/__dev__/useToastMessage';
import type { ColorEncoding } from './useColorSchemeUtils';
import { equals, ifElse, includes, reject } from 'ramda';

type ToastMessageData = MessageToastProps['data'];
type State = Readonly<{
  colorEncoding: ColorEncoding;
  colorThemeIndex: number;
  messages: ToastMessageData;
  schemeIndex: number;
  collapsedSections: string[];
}>;
type Actions =
  | { type: 'clearMessage' }
  | { type: 'copyToClipboard'; copyText: string }
  | { type: 'copyToClipboardError'; copyText: string; errorMessage: string }
  | { type: 'selectEncoding'; encoding: ColorEncoding }
  | { type: 'setColorThemeIndex'; index: State['colorThemeIndex'] }
  | { type: 'setSchemeIndex'; index: State['schemeIndex'] }
  | { type: 'toggleSection'; section: string };

const reducer = (state: State, action: Actions): State => {
  switch (action.type) {
    case 'clearMessage': {
      return { ...state, messages: [] };
    }
    case 'copyToClipboard': {
      const { copyText } = action;
      const messages = toToastMessage(copyText, getCopyMessage(action.copyText));
      return { ...state, messages };
    }
    case 'copyToClipboardError': {
      const { copyText, errorMessage } = action;
      const messages = toToastMessage(copyText, errorMessage);
      return { ...state, messages };
    }
    case 'selectEncoding': {
      return { ...state, colorEncoding: action.encoding };
    }
    case 'setColorThemeIndex': {
      return { ...state, colorThemeIndex: action.index };
    }
    case 'setSchemeIndex': {
      return { ...state, schemeIndex: action.index };
    }
    case 'toggleSection': {
      return {
        ...state,
        collapsedSections: ifElse(
          includes(action.section),
          reject(equals(action.section)),
          (arr: string[]) => [...arr, action.section]
        )(state.collapsedSections)
      };
    }
    default: {
      return state;
    }
  }
};

type Props = {
  defaultColorEncoding?: State['colorEncoding'];
  defaultColorTheme?: number;
};

const useColorTableState = ({ defaultColorEncoding = 'RGB', defaultColorTheme = 0 }: Props) => {
  const [state, dispatch] = useReducer(reducer, {
    colorEncoding: defaultColorEncoding,
    colorThemeIndex: defaultColorTheme,
    messages: [],
    schemeIndex: 0,
    collapsedSections: []
  });

  return {
    state,
    actions: {
      copyToClipboard: (copyText: string) => {
        // do side-effect in callback
        if (isSecureContext)
          return dispatch({
            type: 'copyToClipboardError',
            copyText,
            errorMessage: `Error! Clipboard API is only available in secure contexts (HTTPS).`
          });

        navigator.clipboard.writeText(copyText).then(
          () => {
            dispatch({ type: 'copyToClipboard', copyText });
          },
          (err) => {
            console.warn(err);
            dispatch({
              type: 'copyToClipboardError',
              copyText,
              errorMessage: `Error! Unable to write "${copyText}" to browser clipboard.`
            });
          }
        );
      },
      clearMessage: () => dispatch({ type: 'clearMessage' }),
      selectEncoding: (encoding: ColorEncoding) => dispatch({ type: 'selectEncoding', encoding }),
      setColorThemeIndex: (index: State['colorThemeIndex']) =>
        dispatch({ type: 'setColorThemeIndex', index }),
      setSchemeIndex: (index: State['schemeIndex']) => dispatch({ type: 'setSchemeIndex', index }),
      toggleSection: (section: string) => dispatch({ type: 'toggleSection', section })
    }
  };
};

const getCopyMessage = (str: string) => `Copied ${str} to clipboard`;
const toToastMessage = (key: string, summary: string): ToastMessageData => [
  {
    key,
    data: {
      summary,
      closeAffordance: 'on',
      autoTimeout: 'on'
    }
  }
];

export { useColorTableState };
